<template>
  <div>
    <div class="linkmap">{{linkMap}}</div>
    <el-card class="box-card">
      <el-table
        :data="list"
        stripe
        index="index"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="合同名称"
          width="200">
        </el-table-column>
        <el-table-column
          prop="index"
          label="标签">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status == 0" type="info">草稿</el-tag>
            <el-tag v-if="scope.row.status == 1">待签署</el-tag>
            <el-tag v-if="scope.row.status == 2" type="success">已签署</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="firstParty"
          label="甲方"
          width="400">
        </el-table-column>
        <el-table-column
          prop="partyB"
          label="乙方"
          width="400">
        </el-table-column>
        <el-table-column
          prop="index"
          align="right"
          label="操作">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="$router.push(`${$route.path}/detail/${scope.row.id}`)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>确定删除当前合同?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        dialogVisible: false
      }
    },
    computed: {
      list () {
        return this.$store.state.contractList.filter(item => this.getList(item))
      },
      path () {
        return this.$route.path
      }
    },
    methods: {
      handleDel () {
        this.dialogVisible = true
      },
      getList (item) {
        if (this.path === '/draft') {
          return item.status === 0
        } else if (this.path === '/my') {
          return item.status !== 0 && item.firstParty === this.$store.state.address
        } else if (this.path === '/recive') {
          return (item.status === 1 || item.status === 2) && item.partyB === this.$store.state.address
        }
      }
    }
  }
</script>

<style scoped lang="less">
.card-box {
  min-height: 500px;
}
  ul {
    li {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #ccc;
      height: 40px;
      line-height: 40px;
      padding-left: 20px;
      cursor: pointer;
      &:hover {
        color: #444;
      }
    }
  }
</style>



